.nui-drag__draggable {
  cursor: move;
}
.nui-drag__draggable:hover {
  background-color: var(--nui-color-bg-light-hover,#f2f2f2);
  box-shadow: 0 0 5px 0 var(--nui-shadow-color, rgba(17, 17, 17, 0.3));
}
.nui-drag__drag-source {
  position: relative;
}
.nui-drag__drag-source--overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--nui-color-overlay-light,rgba(255, 255, 255, 0.9));
  z-index: 2;
  transition: background-color ease 0.3s;
  outline: 0;
}
.nui-drag__drag-container {
  position: fixed;
  z-index: 2000;
  pointer-events: none;
}
.nui-drag__drag-halo {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-color: var(--nui-color-selected-hover,rgba(0, 196, 210, 0.2));
  position: relative;
  top: -25px;
  left: -25px;
  pointer-events: none;
}
.nui-drag--dragging {
  background-color: rgba(0, 196, 210, 0.1) !important;
  background-color: var(--nui-color-selected, rgba(0, 196, 210, 0.1)) !important;
  z-index: 2001 !important;
  position: relative;
  top: -40px;
  pointer-events: none;
  box-shadow: 0 0 5px 0 var(--nui-shadow-color, rgba(17, 17, 17, 0.3));
}
.nui-resize-gutter {
  position: absolute;
  display: flex;
  top: 0;
  z-index: 1;
}
.nui-resize-gutter-disabled {
  pointer-events: none;
}
.nui-resize-gutter--hovering:not(.nui-resize-gutter--active) {
  background-color: var(--nui-color-selected,rgba(0, 196, 210, 0.1));
}
.nui-resize-gutter--hovering:not(.nui-resize-gutter--active) .nui-resize-gutter__split {
  background-color: var(--nui-color-selected-contrast,#00c4d2);
}
.nui-resize-gutter--active {
  background-color: var(--nui-color-selected-active,rgba(0, 196, 210, 0.2));
}
.nui-resize-gutter--active .nui-resize-gutter__split {
  background-color: var(--nui-color-selected-contrast,#00c4d2);
}
.nui-resize-gutter__split-horizontal {
  width: 2px;
  height: 100%;
  margin: 0 auto;
}
.nui-resize-gutter__split-vertical {
  height: 2px;
  width: 100%;
  margin: auto 0;
}
