@font-face {
    font-family: 'Source Code Pro';
    src: url('../fonts/SourceCodePro-Regular.ttf.woff2') format('woff2'),
    url('../fonts/SourceCodePro-Regular.ttf.woff') format('woff');
    font-weight: @nui-font-weight-regular;
    font-style: normal;
}
