  /** other customziations */
}
.nui-dnd-dropzone.nui-dnd-dropzone--active {
  border: 4px dashed #999 !important;
}
.nui-dnd-dropzone.nui-dnd-dropzone--drop-allowed {
  cursor: move;
  border: 4px solid #00c4d2 !important;
  background-color: rgba(0, 196, 210, 0.2);
}
.nui-dnd-dropzone.nui-dnd-dropzone--drop-not-allowed {
  cursor: move;
  border: 4px solid #dd2c00 !important;
  background-color: rgba(221, 44, 0, 0.2);
}
.nui-dnd-dropzone .centered-overlay,
.nui-dnd-dropzone .nui-icon-item:before {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.nui-dnd-dropzone .nui-icon-item:before {
  border-radius: 50%;
  content: "";
  display: block;
  height: 40px;
  width: 40px;
  z-index: -1;
}
.nui-dnd-dropzone .drop-circle-permanent .nui-icon-item:before {
  background-color: #fff;
}
.nui-dnd-dropzone .drop-circle-permanent .nui-icon-item svg polygon {
  fill: #111;
}
.nui-dnd-dropzone .drop-circle-allowed .nui-icon-item:before {
  background-color: #00c4d2;
}
.nui-dnd-dropzone .drop-circle-allowed .nui-icon-item svg polygon {
  clip-path: circle(5px);
  fill: #fff;
}
.nui-dnd-dropzone .drop-circle-not-allowed .nui-icon-item:before {
  background-color: #dd2c00;
}
.nui-dnd-dropzone .drop-circle-not-allowed .nui-icon-item svg path {
  clip-path: circle(5px);
  fill: #fff;
}
